import wx from 'weixin-js-sdk'
export default {
  name: 'loginError',
  mounted () {
    document.addEventListener(
      'WeixinJSBridgeReady',
      function () {
        wx.closeWindow()
      },
      false
    )
    wx.closeWindow()
  }
}



